import { useState, useEffect, memo } from "react";

function Paginate({ totalList, currentPage, setCurrentPage }) {
  console.log(" !");
  const recordPerPage = 16; //  12 
  const totalPage = Math.ceil(totalList / recordPerPage); //  
  const totalPageArray = [...new Array(totalPage + 1).keys()].slice(1); //  

  const [showPages, setShowPages] = useState([]); //  
  const [startIdx, setStartIdx] = useState(0); //
  const [endIdx, setEndIdx] = useState(5); //

  //   
  const getPageNumber = () => {
    const currentArr = totalPageArray.slice(startIdx, endIdx);
    setShowPages(currentArr);
  };

  useEffect(() => {
    getPageNumber();
  }, [startIdx, endIdx, totalList]);

  if (totalList <= 16) {
    //  16    
    return null;
  }

  return (
    <div className="pagination_area">
      <button
        disabled={currentPage === 1 ? true : false}
        onClick={() => {
          setCurrentPage(currentPage - 1);
          if (currentPage % 5 == 1) {
            setStartIdx(startIdx - 5);
            setEndIdx(startIdx);
          } //1, 6, 11..
        }}
      >
        prev
      </button>
      <ul className="pagination">
        {showPages.map((pageNm) => {
          return (
            <li
              key={pageNm}
              onClick={() => {
                setCurrentPage(pageNm);
              }}
              style={{ fontWeight: pageNm === currentPage ? "bold" : "normal" }}
            >
              {pageNm}
            </li>
          );
        })}
      </ul>
      <button
        disabled={currentPage === totalPage ? true : false}
        onClick={() => {
          setCurrentPage(currentPage + 1);
          if (currentPage % 5 == 0) {
            setStartIdx(endIdx);
            setEndIdx(endIdx + 5);
          }
        }}
      >
        next
      </button>
    </div>
  );
}

const memoizedPaginate = memo(Paginate);
export default memoizedPaginate;
